import React, { Fragment } from 'react';
// import { NavLink } from 'react-router-dom';
import { Link } from "gatsby"
import { Icon } from 'antd';
import user from '../../assets/mypic.jpg';
import bg from '../../assets/bg2.jpg';
import { graphql } from "gatsby"
import Img from "gatsby-image"
const SideNav = ({data}) => (
    <Fragment>
    <div className="header">
          <div>
            <div className="header-image">
               <Img fixed={(data)?data.file.childImageSharp.fixed:null} />
            </div>
          </div>
          <h1 className="header-name">Roopam Garg</h1>
          <p className="header-title">Full Stack Web Developer</p>
          <div className="header-dash"></div>
        </div>
        <div className="layout-nav">
          <ul className="nav">
            <li className="nav-item"><Link to="/" exact activeClassName="active-link">About</Link></li>
            <li className="nav-item"><Link to="/experience" activeClassName="active-link">Experience</Link></li>
            <li className="nav-item"><Link to="/projects" activeClassName="active-link">Projects</Link></li>
            <li className="nav-item"><Link to="contact" activeClassName="active-link">Contact</Link></li>
          </ul>
        </div>
        
        <div className="layout-social-nav">
          <ul className="social-nav">
            <li className="nav-item">
              <a target="_blank" href="https://www.instagram.com/_roopamgarg/">
                <Icon type="instagram" />
              </a>
            </li>
            <li className="nav-item">
              <a target="_blank" href="https://github.com/roopamgarg">
                <Icon type="github" />
              </a>
            </li>
            <li className="nav-item">
              <a
                target="_blank"
                href="https://www.linkedin.com/in/roopam-garg-8135a8158/"
              >
                <Icon type="linkedin" />
              </a>
            </li>
            <li className="nav-item">
              <a target="_blank" href="https://twitter.com/roopamg777">
                <Icon type="twitter" />
              </a>
            </li>
          </ul>
        </div>
    </Fragment>
)

export default SideNav;